import {
	mapGetters,
	mapActions,
	mapState
} from 'vuex';

import {UserModel} from '../../model/user.js'
import {OrderModel} from '../../model/order.js'
const userModel = new UserModel();
const orderModel = new OrderModel();
export default {

	// computed: {
	// 	// ...mapGetters(['statusBarHeight'])
	// 	...mapState(['statusBarHeight'])
	// },
	data() {
		return {
			trade_pwd: '',
			focus: true,
			show: false,
			numarr: [],
			d_time: 10,
			nextsteep: false,
			tel: 0,
			numlength: "",
			gopen: false,
			isactive: true,
			resgetcode: false,
			boardlists: [{
					id: 1,
					con: 1,
					checked: false
				}, {
					id: 2,
					con: 2,
					checked: false
				}, {
					id: 3,
					con: 3,
					checked: false
				}, {
					id: 4,
					con: 4,
					checked: false
				}, {
					id: 5,
					con: 5,
					checked: false
				}, {
					id: 6,
					con: 6,
					checked: false
				}, {
					id: 7,
					con: 7,
					checked: false
				}, {
					id: 8,
					con: 8,
					checked: false
				}, {
					id: 9,
					con: 9,
					checked: false
				}, {
					id: 10,
					con: "",
					checked: false
				}, {
					id: 11,
					con: 0,
					checked: false
				},
				{
					id: 12,
					con: "x",
					checked: false
				},

			],
			order_id:0,
			internal_payment_sn:''
		};
	},
	computed: {
		len: function() {
			if (this.trade_pwd.length == 6) {
				// 第一次进来可以点击的

			}
			return this.trade_pwd.length
		}
	},
	
	methods: {
		writepwd(num) {
			if (num == 12) {
				this.close();
				return;
			}
			if (num == 10) {
				return;
			}
			if (num == 11) {
				num = 0;
				this.boardlists[10].checked = true;
			} else {
				this.boardlists[num - 1].checked = true;
			}
			this.numarr.push(num);
			if (this.numarr.length > 6) {
				return
			}
			var that = this;
			setTimeout(function() {
				for (var i = 0; i < that.boardlists.length; i++) {
					that.boardlists[i].checked = false;
				}
			}, 200)
			this.trade_pwd = this.numarr.join("");


		},
		//密码框
		back() {
			uni.navigateBack();
		},
		//回退方法
		close() {
			this.numarr.pop();
			if (this.trade_pwd.length > 0) {
				this.trade_pwd = this.trade_pwd.substring(0, this.trade_pwd.length - 1);
			}
		},
		blur1() {
			this.show = false
		},
		focus1() {

			this.show = true
		},

		// 确认执行的方法
		setpwd() {
			if (this.trade_pwd.length < 6) {
				console.log('密码长度不能少于6位');
				return;
			}
			// 密码长度为6位以后执行方法
			console.log(this.trade_pwd);
			let params = {
				password : this.trade_pwd
			}
			userModel.checkPassword(params).then((res) => {
					let data = res.data
					if (data.status) {
						console.log(this.internal_payment_sn)
						if (this.order_id) {
							params = {
								order_id:this.order_id
							}
						}
						if (this.internal_payment_sn) {
							console.log('进来了')
							params = {
								internal_payment_sn:this.internal_payment_sn
							}
						}
						
						orderModel.yuonpay(params).then((res) => {
							if (res.code == 200) {
								uni.showToast({
									title:'支付成功',
								})
								let url;
								if (this.internal_payment_sn) {
									url = `/pagesC/payment_finish/payment_finish?internal_payment_sn=${this.internal_payment_sn}`
								} else {
									url = `/pagesC/payment_finish/payment_finish?order_id=${this.order_id}`
								}
								setTimeout(()=>{
									uni.navigateTo({
										url: url
									})
								},2000)
							}
						})
						
					} else {
						uni.showToast({
							title:'密码错误',
							icon:'none'
						})
					}
			})
			
		},

	},
	onLoad:function(opiton) {
		console.log(opiton)
		if (opiton.order_id) {
			this.order_id = opiton.order_id
		}
		if (opiton.internal_payment_sn) {
			this.internal_payment_sn = opiton.internal_payment_sn
		}
	},

};
